import React from 'react';

import { Link } from 'react-router-dom';
import { ReactComponent as HiIm } from '../../../assets/01.svg';
import { ReactComponent as Fernando } from '../../../assets/02A.svg';
import { ReactComponent as Fregate } from '../../../assets/02B.svg';
import { ReactComponent as Plus } from '../../../assets/03.svg';
import { ReactComponent as AboutMe } from '../../../assets/04.svg';
import { ReactComponent as CreativeDirector } from '../../../assets/05.svg';
import { ReactComponent as Currently } from '../../../assets/06.svg';
import './style.scss';

class Header extends React.Component {

  componentDidMount() {
  }

  render() {
    return (
      <header className="header">
        <div className="main">
          <div className="hi">
            <HiIm />
          </div>
          <div className='name'>
            <Fernando />
          </div>
          <div className="last-name-about">
            <div className="last-name">
              <Fregate />
            </div>
            <div className="about">
              <Link to="/about-me" className="me">
                <Plus />
                <AboutMe />
              </Link>
            </div>
          </div>
          <div className="description-box">
            <div className="creative-director">
              <CreativeDirector />
            </div>
            <div className="currently">
              <Currently />
            </div>
          </div>
        </div>

      </header>
    );
  }
}

export default Header;