import React from 'react';

import { Link } from 'react-router-dom';

import Header from './Header/index';

import T1Img from '../../assets/T1.jpg';
import T2Img from '../../assets/T2.jpg';
import T3Img from '../../assets/T3.jpg';
import T4Img from '../../assets/T4.jpg';
import T5Img from '../../assets/T5.jpg';
import T6Img from '../../assets/T6.jpg';

import './style.scss';

class Home extends React.Component {


  render() {
    return (
      <div className="home" >
        <div className="container-home">
          <Header />
          <div className="campanha">
            <Link to="/whiskas" className="whiskas">
              <img src={T1Img} alt="Whiskas - Curiosity Box" />
            </Link>
          </div>
          <div className="campanha">
            <Link to="/boticario" className="boticario">
              <img src={T2Img} alt="Boticrio - Intense Carnaval" />
            </Link>
          </div>
          <div className="campanha">
            <Link to="/havaianas" className="havaianas">
              <img src={T3Img} alt="Havaianas - Dia das bruxas" />
            </Link>
          </div>
          <div className="campanha">
            <Link to="/doritos" className="doritos" >
              <img src={T4Img} alt="Doritos - Trigonometria" />
            </Link>
          </div>
          <div className="campanha">
            <Link to="/burger-king" className="burger-king">
              <img src={T5Img} alt="Burger king - Flame Hour" />
            </Link>
          </div>
          <div className="campanha">
            <Link to="/estadao" className="estadao">
              <img src={T6Img} alt="Estado - Cannes Winners" />
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
